.pitcher {
    width: 86px;
    line-height: 100px;
    font-size: 14px;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
        -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

.hasMilk {
    position: absolute;
    top: 0;
    left: 0;
    right: 16%;
    margin: 0 auto;
    color: white;
}

.milkStatus {
    position: absolute;
    top: 14px;
    left: 0;
    right: 16%;
    margin: 0 auto;
    color: white;
}

.pitcherImg {
    vertical-align: bottom;
    height: auto;
    width: 100%;
}


