.cup {
    width: 86px;
    line-height: 100px;
    font-size: 9px;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.hasBrown {
    position: absolute;
    top: 30%;
    left: 0;
    right: 16%;
    margin: 0 auto;
}

.hasSyrup {
    position: absolute;
    top: 11%;
    left: 0;
    right: 16%;
    margin: 0 auto;
}

.cupHasMilk {
    position: absolute;
    top: -7%;
    left: 0;
    right: 17%;
    margin: 0 auto;
}

.cupImg {
    height: auto;
    width: 100%;
}


