.milk {
    width: 86px;
    position: absolute;
    bottom: 0;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.milkImg {
    height: auto;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.halfn {
    height: auto;
    width: 50%;
}


